body {
  background-color: #e8dfff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.chat-container {
  background-color: #f7edff;
  padding: 80px;
  border-radius: 8px;
  text-align: left;
  border: 4px solid  #a40af0;
}

.mes {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.user-message {
  background-color: #ffbdf9;
  padding: 5px 12px;
  border-radius: 8px;
  font-size: 12px;
  font-weight: 700;
  margin-right: 10px;
  color: #fcf9f6;
  white-space: nowrap; /* Prevent wrapping to a new line */
}

.input-container {
  border: 3px solid #ffbdf9;
  background-color: #fcf9f6;
  outline: none;
  padding: 5px 10px;
  color: #a40af0;
  font-size: 12px;
  border-radius: 10px;
  flex-grow: 1; /* Allow the input container to grow and fill available space */
}


.ai-message {
  background-color: #f5e5ff;
  padding: 8px;
  border-radius: 4px;
}

.title {
  color: #a40af0;
  margin-bottom: 10px;
  font-size: 22px;
}

.loading-text {
  width: 15%;
  margin-top: 10px;
  border: 3px solid #ffbdf9;
  background-color: #fcf9f6;
  padding: 5px 10px;
  color: #a40af0;
  font-size: 12px;
  border-radius: 10px;
  text-align: center;
}

.response-alert {
  background-color: #ffbdf9;
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  margin-right: 10px;
  margin-top: 12px;
  color:#fcf9f6;
  font-weight: 700;
}

.but {
  display: flex;
  justify-content: center;
  margin-top: 12px;
}

.load{
  display: flex;
  justify-content: center;
}




.btn {
  display: inline-block;
  width: fit-content;
  border: 3px solid  #a40af0;
  padding: 6px 12px;
  border-radius: 10px;
  text-decoration: dashed;
  background-color: #e8dfff;
  color: #fcf9f6;
  font-weight: 700;
  transition: background 0.5s, color 0.5s;
  align-items: center;
}

.btn:hover {
  background:  #a40af0;
  color: #fcf9f6;
}

/* Add these styles to your App.css */
.category-buttons {
  display: flex;
  justify-content: space-between;
}

.category-buttons .btn[disabled] {
  background-color: #dcdcdc; /* Gray color for disabled buttons */
  color: #666; /* Text color for disabled buttons */
  cursor: not-allowed;
}

.start-new-button {
  text-align: center;
  margin-top: 20px; /* Adjust the margin-top as needed */
}

.selected {
  background-color: #007bff;
  color: #fff;
}

.body-sent-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  /* Add animation properties for fading out */
  transition: opacity 0.9s ease, max-height 0.9s ease, padding 0.8s ease;
  opacity: 1;
  max-height: 1000px; /* Adjust this value based on your button size and layout */
  padding: 10px;
}

.body-sent-buttons.hidden {
  opacity: 0;
  max-height: 0;
  padding: 0;
}
